@import-normailize; 
@import "helper";

*{margin: 0;padding: 0;}
*{box-sizing: border-box;}
*::before{box-sizing: border-box;}
*::after{box-sizing: border-box;}
ul,ol{
    list-style: none;
}
a{ //a标签
    text-decoration: none;
    color: inherit;
}
body{
    font-family: $font-hei;
    font-size: 16px;
    line-height: 1.2;
    background: #f5f5f5;
}
button,input{
    font: inherit;
}
:focus{
    outline:none; 
}
.icon{
    width: 1em;
    height: 1em;

}
.clearfix::after{
    content: '';
    display: block;
    clear: both;
}
.oneLine{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}